import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import PasswordIcon from "@mui/icons-material/Password";

import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import axios from "axios";
import {
  Alert,
 
  InputAdornment,
  Snackbar,
  Stack,
} from "@mui/material";
import {  useNavigate } from "react-router-dom";



const theme = createTheme();
export default function Forget() {
  const [error, setError] = React.useState("");
  const history = useNavigate();
  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false)
  const [loading,setLoading] = React.useState(false)
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setLoading(true)
    setError("");
    if (!navigator.onLine) {
        setOpen(true);
      setError(
        "Si la connexion n'est pas active, vrifiez la connexion"
      );
    }
    
  
    
      const data = new FormData(event.currentTarget);
    console.log({
      email: data.get("email"),
     
    });
  
       axios
      .post(`${process.env.REACT_APP_URL_USER}/forget`, {
        email: data.get("email")
        
      })
      .then((res) => {
        console.log(res.data.message);
        setsuccees(res.data.message); 
        setLoading(true)
        setResToHome(true)
        setOpen(true);

      
      })
         .catch((error) => {
           console.log(error.response.data.message)
           
           setError(error.response.data.message)
           setLoading(false)
           setOpen(true);
               setResToHome(false)
        //  setError(error.response.data.message);
  
      });
    

 
  };

  return (
      <ThemeProvider theme={theme}>
      
      <Grid container component="main" sx={{ height: "100vh" }}>
        
              <CssBaseline />
              
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: `url('./assets/login.jpg')`,
            objectFit:'cover',
            backgroundRepeat: "no-repeat",
            backgroundColor: (t) =>
              t.palette.mode === "light"
                ? t.palette.grey[50]
                : t.palette.grey[900],
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        />

        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            <PasswordIcon/>
            </Avatar>
            <Typography  component="h1" variant="h6" textTransform="uppercase">
          Mettez votre adress mail
            </Typography>
            <Box
              component="form"
              noValidate
              onSubmit={handleSubmit}
              sx={{ mt: 1 }}
            >
              {loading ?
                <Paper elevaton={3}>

                  <Typography variant='body1' sx={{
                    margin: '15px', color: "green",padding:'5px',textAlign:'justify'
                  
                  }} gutterBottom>{succees}</Typography>
                </Paper>
                :
                <TextField
                margin="normal"
                fullWidth
                required
                id="email"
                              label="Email "
                              helperText='Saisissez votre adress mail '
                name="email"
                autoComplete="email"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonOutlineIcon />
                    </InputAdornment>
                  ),
                }}
              />}
              
           
           
              {
                loading ?
              //     <Button
              //   type="submit"
              //   fullWidth
                 
              //     disabled
              //   sx={{
              //     mt: 3, mb: 2, background: {
                
              //   } }}
             
              // >
              //  Envoyer
              //     </Button>
                  
                  // <Typography variant='subtitle2'>{succees}</Typography>
                  ""
                  :
              
                  <Button
                type="submit"
                fullWidth
             
                sx={{
                  mt: 3, mb: 2, background: {
                    md: "#DD9B01", xs: "#DD9B01", "&:hover": {
                      background : "#6D4826"
                    }
                } }}
           variant='contained'
              >
            Rnitialiser le mot de passe   
              </Button>
             }
              
   <Button onClick={() => {
                history('/login')
                    }} >Retour au page principale</Button>
                  
              <Button onClick={() => {
                setLoading(false)
           }}>Je n'ai pas ru le mail ? </Button> 
            </Box>
                  </Box>
                  
        </Grid>
      </Grid>
  { resToHome  ? <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={5000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="success"
            sx={{ width: "100%" }}
          
          >
            {succees} 
          
          </Alert>
        </Snackbar>

      </Stack>
      :
      <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={3000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="error"
            sx={{ width: "100%" }}
          >
            
            {error}
          </Alert>
        </Snackbar>
      </Stack>}
    </ThemeProvider>
  );
}
