import { FormControl, Input, InputAdornment, InputLabel, Typography } from '@mui/material';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setGetUser } from '../../../features/user.reducers';
import { setgetAll } from '../../../features/users.reducers';
import Navbar from '../NavbarAdmin';
import Users from './Users';

const CardUsers = () => {




     const users = useSelector((state) => state.users.users);
  const user = useSelector((state) => state.user.user);
   const [searchValue, setSearchValue] = useState('');
  

     const dispatch = useDispatch();
    const sendRquest = async () => {
      const res = await axios
        .get(`${process.env.REACT_APP_URL_USER}`, {
          withCredentials: true,
        })
        // .catch((err) => console.log(err));
      const data = await res.data;
      return data;
    };
     const sendRquestUser = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
      withCredentials: true
    })
      // .catch(err => console.log(err));
    const data = await res.data;
    return data;
  }
  useEffect(() => {
    sendRquestUser().then((data) => {
      dispatch(setGetUser(data.user))
    });
  },[dispatch])
useEffect(() => {
      sendRquest().then((data) => {
        dispatch(setgetAll(data));
        
      });
    },[dispatch]);
    return (
      <>
      {user?.isAdmin && <div>
          <Navbar />
          <div style={{position:'fixed',top:'10%',left:'0',right:'0',zIndex:'1000'}}>
              <Typography>Rechercher avec code </Typography>
         <FormControl fullWidth sx={{ m: 1 }} variant="standard">
          <InputLabel  htmlFor="standard-adornment-amount">Code Personnel</InputLabel>
          <Input
            value={searchValue} onChange={(e) => setSearchValue(e.target.value)}
            id="standard-adornment-amount"
            startAdornment={<InputAdornment position="start">code</InputAdornment>}
          />
        </FormControl>
          </div>
          <br></br><br></br><br></br><br /><br />
            {users
          ?.filter(item =>{
    return item.identification?.toLowerCase().includes(searchValue.toLowerCase());
  } ).map((el) => {
                return (
                    <Users key={el._id} el={el} /> 
                )
            })}
        </div>}
      </>
    );
};

export default CardUsers;