import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Link from "@mui/material/Link";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import VisibilityIcon from "@mui/icons-material/Visibility";

import PasswordIcon from "@mui/icons-material/Password";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import axios from "axios";
import {
  Alert,
 
  CircularProgress,
 
  InputAdornment,
  Snackbar,
  Stack,
} from "@mui/material";
import { NavLink, useNavigate, useParams } from "react-router-dom";


const theme = createTheme();
export default function Forget() {
  const [error, setError] = React.useState("");

  const [showPassword, setShowPassword] = React.useState(false);

    const history = useNavigate();
    const params= useParams()
  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false)
  const [loading, setLoading] = React.useState(false)
  const [password,setpassword] =  React.useState('')
  const [confirm_password,setConfirmpassword] =  React.useState('')

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    const newPass = data.get("newPass")
 

    if (password !== confirm_password) {
      setOpen(true)
     return setError('Les mots de passe ne correspondent pas')
    }

    if (!navigator.onLine) {
        setOpen(true);
      setError(
        "Si la connexion n'est pas active, vrifiez la connexion"
      );
    }
    
  
    
   
    
     
    
       axios
      .put(`${process.env.REACT_APP_URL_USER}/reset/${params.token}`, {
        newPass: newPass
        
      })
      .then((res) => {
        console.log(res.data.message);
        setLoading(true)
        setResToHome(true)
        setOpen(true)
          setsuccees(res.data.message); 
           setTimeout(() => {
          history("/login");
        }, 5000);

      
      })
         .catch((error) => {
           console.log(error)
        setLoading(false)
        setOpen(true);
         setError(error.response.data.message);
  
      });
    

 
  };

  return (
      <ThemeProvider theme={theme}>
      
      <Grid container component="main" sx={{ height: "100vh" }}>
        
              <CssBaseline />
              
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: `url('./assets/login.jpg')`,
            objectFit:'cover',
            backgroundRepeat: "no-repeat",
            backgroundColor: (t) =>
              t.palette.mode === "light"
                ? t.palette.grey[50]
                : t.palette.grey[900],
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        />

        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            <PasswordIcon/>
            </Avatar>
            <Typography  component="h1" variant="h6" textTransform="uppercase">
Choisissez un nouveau mot de passe.
            </Typography>
            <Box
              component="form"
              noValidate
              onSubmit={handleSubmit}
              sx={{ mt: 1 }}
            >
             <TextField
                margin="normal"
                required
                fullWidth
                name="newPass"
                label="Mot de passe"
                value={password}
                onChange={(e) => {
                  setpassword(e.target.value)
                }}
                id="newPass"
                // autoComplete="current-password"
                endIcon={<VisibilityIcon />}
                type={showPassword ? "text" : "password"}
                InputProps={{
                 
                  endAdornment: (
                    <InputAdornment
                      onClick={() => {
                        setShowPassword(!showPassword);
                      }}
                      sx={{ cursor: "pointer" }}
                      position="end"
                    >
                      <VisibilityOff />
                    </InputAdornment>
                  ),
                }}
                          />
                           <TextField
                margin="normal"
                required
                fullWidth
                name="confirmpassword"
                label="confirmer votre mot de passe"
           value={confirm_password}
                onChange={(e) => {
                  setConfirmpassword(e.target.value)
                }}
               
                id="confirmpassword"
                // autoComplete="current-confirm-password"
                endIcon={<VisibilityIcon />}
                type={showPassword? "text" : "password"}
               
              />
           <NavLink to="/login">
                    <Link variant="subtitle2">Retour au page principale</Link>
                  </NavLink>
           
              {



                
                
                loading ?
               <Box sx={{ display: 'flex',alignItems:'center',justifyContent:'center' }}>
      <CircularProgress />
    </Box>: <Button
                type="submit"
                fullWidth
             
                sx={{
                  mt: 3, mb: 2, background: {
                    md: "#DD9B01", xs: "#DD9B01", "&:hover": {
                      background : "#6D4826"
                    }
                } }}
           variant='contained'
              >
               envoyer
              </Button>
             }
              
             

            </Box>
                  </Box>
                  
        </Grid>
      </Grid>
  { resToHome  ? <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={15000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="success"
            sx={{ width: "100%" }}
          
          >
            {succees} 
          
          </Alert>
        </Snackbar>

      </Stack>
      :
      <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={15000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="error"
            sx={{ width: "100%" }}
          >
            
            {error}
          </Alert>
        </Snackbar>
      </Stack>}
    </ThemeProvider>
  );
}
