import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';

import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import axios from 'axios';
import { Button, TextField, Typography } from '@mui/material';
import { Box } from '@mui/system';
import { sendRquest } from '../../../app/verify';
import { setgetAll } from '../../../features/users.reducers';
import { useDispatch } from 'react-redux';
export default function BasicTable({ el }) {
  const dispatch = useDispatch()
  const [updateFoem, setUpdateForm] = React.useState(false)
  const [note,setnote] = React.useState('')
    const handleSubmit = (event) => {
  
    
  event.preventDefault()
       axios
         .post(`${process.env.REACT_APP_URL_USER}/note/${el?._id}`, {
        note : note
      })
         .then((res) => {
         sendRquest().then((data) => {
        dispatch(setgetAll(data));
        
      });
          //  alert(res.data.message)
           setUpdateForm(false)
      })
         .catch((error) => {
           alert(error.response.data)
           console.log(error.response.data);
           
      });
    

 
  };
  
  return (

    <TableContainer component={Paper}>
      
      
        <Table aria-label="simple table">
        <TableBody>
        
            <TableRow
              key={el?._id}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell width='100px' component="th" scope="row">
                {el?.firstName}
              </TableCell>
            <TableCell width='200px' align="left">{el?.lastName}</TableCell>
              <TableCell width='100px' align="left">{el?.identification}</TableCell>
            {el?.average === "" && <TableCell width='100px' align="center">
              {
                <>
                  
                  <Box>
                   <TextField
                    required
              
                                      label='note'
                    margin='normal' type={'text'} variant='outlined' placeholder='note'
                    value={note} onChange={(e) => setnote(e.target.value)}
                  />
                    <Button onClick={handleSubmit}>Mettre sa note</Button>
                </Box>
                </>
              }
            </TableCell>}  
         
            


            {el?.average !== "" && <TableCell onClick={() => {
              setUpdateForm(!updateFoem)
            }}
              width='100px' align="center"><Typography color='green'>{el?.average}</Typography></TableCell>}
           
            {
              updateFoem &&  <>
                  
                  <Box>
                   <TextField
                    required
              
                                      label='note'
                    margin='normal' type={'text'} variant='outlined' placeholder='note'
                    value={note} onChange={(e) => setnote(e.target.value)}
                  />
                    <Button onClick={handleSubmit}>Mettre sa note</Button>
                </Box>
                </>
           }
            </TableRow>
          
        </TableBody>
      </Table>
    </TableContainer> 
  );
}
