import * as React from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import StepContent from '@mui/material/StepContent';
import Button from '@mui/material/Button';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Navbar from '../../components/Navbar'
import FixedBottomNavigation from '../../components/BottomBar'
import axios from 'axios';
import { useSelector } from 'react-redux';
import { Alert, CircularProgress, Snackbar } from '@mui/material';
import { Stack } from '@mui/system';

const steps = [
  {
    label: 'Responsabilits de chaque membre',
    description: `Chaque membre du groupe est responsable de sa participation aux rptitions et aux performances, ainsi que de la prparation et de l'apprentissage des chansons.`,
  },
  {
    label: 'Horaires de rptition',
    description:
      `Les rptitions auront lieu  un horaire convenu  l'avance et seront obligatoires pour tous les membres. Les absences doivent tre signales  l'avance et justifies.`,
  },
  {
    label: 'Matriel',
    description: ` Chaque membre est responsable de son propre matriel et doit veiller  ce qu'il soit en bon tat de fonctionnement pour les rptitions et les performances.`,
  },
  {
    label: 'Arrangements de performances ',
    description: ` Les arrangements pour les performances, y compris les dates, les lieux et les horaires, seront discuts et convenus en groupe. Les membres doivent tre disponibles pour toutes les performances prvues.`,
  },
  {
    label: 'Rgles de conduite',
    description: ` Tous les membres doivent se comporter de manire professionnelle et respectueuse envers les autres membres du groupe, les fans et les employeurs. Les comportements inappropris, tels que l'utilisation de drogues ou d'alcool avant ou pendant les performances, ne sont pas tolrs.`,
  },
];
const stepsCartedeux = [
  {
    label: 'Vrification',
    description: `Pour diter votre carte d'identification personnelle, vous devez d'abord vrifez si vos identifiants inscrits sur la carte sont complets(nom,prnom,email,tl etc).Si ce n'es la cas allez dans profil et mettez le  jour`,
    img : `./procedure/carte.png`
  },
  {
    label: 'Payement',
    description:
      ` Une fois aprs vrification, vous pouvez procder au paiement via FedaPay. Aprs avoir effectu le paiement, vous devrez remplir un formulaire en indiquant l'ID de transaction associ  votre paiement.`,
     img : `./procedure/payement.png`
  },
  {
    label: 'Statut',
    description: `Une fois que vous avez soumis le formulaire, votre statut sera mis  jour pour indiquer "Attente". Cela signifie que votre demande est en cours d'examen et que vous devrez attendre les 24 heures suivantes pour savoir si votre demande a t approuve..`,
        img : `./procedure/attente.png`
  },
  {
    label: 'Validation ',
    description: `Si votre demande est approuve aprs les 24 heures, votre statut sera mis  jour pour indiquer "Russi". Vous pourrez alors imprimer votre carte d'identification personnelle en utilisant les instructions fournies. Il est important de suivre toutes les tapes correctement pour garantir le succs de votre demande.`,
        img : `./procedure/reussi.png`
  },

];

export default function VerticalLinearStepper() {
    const [activeStep, setActiveStep] = React.useState(0);
  const [activeStepsCartedeux, setActiveStepsCartedeux] = React.useState(0);
  const [loading,setLoading] = React.useState(false)
  const [open,setOpen] = React.useState(false)
  const [error,setError] = React.useState('')
  const [succees,setSucees] = React.useState('')
  const [response,setResponse] = React.useState(false)
  const user = useSelector(state => state.user.user)
   const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  const generatePdf = () => {
      setLoading(true)
        axios.post(`${process.env.REACT_APP_URL_USER}/pdf/get/${user?._id}`).then((res) => {
          setLoading(false)
          setSucees(res.data.message)
          setResponse(true)
              setOpen(true);
        }).catch((err) => {
          setLoading(false)
          setError(err.response.data.message)
          setResponse(false)
              setOpen(true);
          
    })
}
  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };
  const handleNextCarte = () => {
    setActiveStepsCartedeux((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBackCarte = () => {
  setActiveStepsCartedeux((prevActiveStep) => prevActiveStep - 1);
  };

  const handleResetCarte = () => {
   setActiveStepsCartedeux(0);
  };

  return (
      <div style={{position:'relative'}}>
           <Box fullwidth>
          <Navbar />
         
          <Typography variant='h6' textAlign='center' sx={{textDecoration:'underline'}}  >
          Le rglement intrieur du groupe
          </Typography>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((step, index) => (
          <Step key={step.label}>
            <StepLabel
              optional={
                index === 4 ? (
                  <Typography variant="caption">Last step</Typography>
                ) : null
              }
            >
            <Typography variant="subtitle1">  {step.label} </Typography>
            </StepLabel>
            <StepContent>
              <Typography variant='subtitle2'>{step.description}</Typography>
              <Box sx={{ mb: 2 }}>
                <div>
                  <Button
                    variant="outlined"
                    onClick={handleNext}
                    sx={{ mt: 1, mr: 1 }}
                    size='small'
                  >
                    {index === steps.length - 1 ? 'Fin' : 'Continue'}
                  </Button>
                  <Button
                    disabled={index === 0}
                    onClick={handleBack}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    retour
                  </Button>
                </div>
              </Box>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      {activeStep === steps.length && (
        <Paper square elevation={0} sx={{ p: 3 }}>
          <Typography>Toutes les tapes sont termines - vous avez termin</Typography>
          <Button onClick={handleReset} sx={{ mt: 1, mr: 1 }}>
            Reset
          </Button>
        </Paper>
        )}
        <br></br>
        <Typography variant='subtitle2'> Ce rglement intrieur peut tre modifi  tout moment si cela est ncessaire pour le bon fonctionnement du groupe.</Typography> 
        <br></br>
        {loading ? <Box sx={{ display: 'flex',alignItems:'center',justifyContent:'center' }}>
      <CircularProgress />
        </Box> : <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }} >
       <Button variant='contained' onClick={generatePdf}>Recevoir le document par mail</Button>
        
        </Box>}
      </Box>
        <br></br>
         <Box fullwidth>
         
          <Typography variant='h6' textAlign='center' sx={{textDecoration:'underline'}}  >
    Comment diter sa carte
          </Typography>
      <Stepper activeStep={activeStepsCartedeux} orientation="vertical">
        {stepsCartedeux.map((step, index) => (
          <Step key={step.label}>
            <StepLabel
              optional={
                index === 4 ? (
                  <Typography variant="caption">Last step</Typography>
                ) : null
              }
            >
            <Typography variant="subtitle1">  {step.label} </Typography>
            </StepLabel>
            <StepContent>
              <Typography variant='subtitle2'>{step.description}</Typography>
              <Box sx={{ mb: 2 }}>
                <img src={step.img} alt='img' style={{width:'100%',height:'150px'}} />
                <div>
                  <Button
                    variant="outlined"
                    onClick={handleNextCarte}
                    sx={{ mt: 1, mr: 1 }}
                    size='small'
                  >
                    {index === stepsCartedeux.length - 1 ? 'Fin' : 'Continue'}
                  </Button>
                  <Button
                    disabled={index === 0}
                    onClick={handleBackCarte}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    retour
                  </Button>
                </div>
              </Box>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      {activeStepsCartedeux === stepsCartedeux.length && (
        <Paper square elevation={0} sx={{ p: 3 }}>
          <Typography>Toutes les tapes sont termines - vous avez termin</Typography>
          <Button onClick={handleResetCarte} sx={{ mt: 1, mr: 1 }}>
            Reset
          </Button>
        </Paper>
          )}
     
      </Box>
       {response ? <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={2000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="success"
            sx={{ width: "100%" }}
          >
            {succees} 
          
          </Alert>
        </Snackbar>

      </Stack>
      :
      <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={7000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="error"
            sx={{ width: "100%" }}
          >
            
            {error}
          </Alert>
        </Snackbar>
      </Stack>}
            <FixedBottomNavigation />
      </div>
  );
}
