import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import CameraAltIcon from "@mui/icons-material/CameraAlt";
import { useDispatch, useSelector } from "react-redux";
import { Alert, IconButton, Snackbar } from "@mui/material";
import { NavLink, useNavigate } from "react-router-dom";
import LogoutIcon from "@mui/icons-material/Logout";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import axios from "axios";
import { logOut, uploadPicture } from "../features/user.reducers";
import { getUserWithVerifyToken, sendRquest } from "../app/verify";

export default function ImageAvatars() {
  //State global
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user?.user);
  //state local
  const [file, setFile] = React.useState(null);
  const [error, setError] = React.useState("");
  const [open, setOpen] = React.useState(false);
  const [opens, setOpens] = React.useState(false);
  const history = useNavigate();
  /**Mui function  */
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleCloses = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpens(false);
  };

  //LogOut function
  const sendlogOutRequest = async () => {
    const res = await axios.post(
      `${process.env.REACT_APP_URL_USER}/logout`,
      null,
      {
        withCredentials: true,
      }
    );
    if (res.status === 200) {
      return res;
    }
    return new Error("Dconnexion chou, reprenez svp");
  };
  const handleLogOut = () => {
    sendlogOutRequest()
      .then(() => {
        setOpen(false);
        dispatch(logOut());
        history("/");
      })
      .catch((error) => {
        setOpen(false);
        setOpens(true);
        if (error.response.data.message) setError(error.response.data.message);
      });
  };
  //Upload function

  const handlePicture = async (e) => {
    const data = new FormData();
    data.append("name", user.names);
    data.append("userId", user._id);
    data.append("user", file);
    // Effectuez une requte POST via Axios
    const res = await axios
      .post(`${process.env.REACT_APP_URL_USER}/upload`, data, {
        withCredentials: true,
      })
      .then((response) => {
        axios.get(`${process.env.REACT_APP_URL_USER}/jwt`);
        console.log(response);
        setFile(null);
      })
      .catch((error) => {
        console.log(error);
        setOpens(true);
        if (error.response.data.message) setError(error.response.data.message);
        else setError("Veuillez ressayer plustard");
      });
    const datas = await res.data;
    return datas;
    // await axios
    //   .post(
    //     `${process.env.REACT_APP_URL_USER}/upload`,

    //     data
    //   )
    //   .then(() => {
    //     dispatch(uploadPicture());
    //   })
    //   .catch(() => {
    //     setOpens(true);
    //     if (error.response.data.message) setError(error.response.data.message);
    //     else setError("Veuillez ressayer plustard");
    //   });
  };
  const uploadPictures = async (e) => {
    e.preventDefault();
    if (file?.size > 7000000) {
      setOpens(true);
      setFile(null);
      return setError(
        `La taille du fichier ne devrait pas dpasser 7 mo, veuillez en choisir un autre`
      );
    }
    handlePicture().then((data) => {
      dispatch(uploadPicture(data));
      setFile(null);
    });
  };
  /**Souscription */
  const souscription = async () => {
    await axios
      .post(`${process.env.REACT_APP_URL_USER}/souscription/${user._id}`)
      .then((res) => console.log(res.data.message))
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <>
      {user?.isAdmin && (
        <NavLink to="/admin">
          <Button>Administrateur</Button>
        </NavLink>
      )}

      {user?.isAdminPupitre && (
        <NavLink to="/admin/home-note">
          <Button>Evaluer vos lves</Button>
        </NavLink>
      )}

      <Button>Jouer au jeu</Button>
      <Stack
        direction="row"
        sx={{
          alignItems: "center",
          justifyContent: "space-between",
        }}
        spacing={2}
      >
        <form onSubmit={uploadPictures}>
          <div className="upload">
            <Button
              sx={{
                position: "relative",
                padding: "11px 16px",
                fontSize: "15px",
                lineHeight: "1.5",
                borderRadius: "3px",
                border: "0",
                overflow: "hidden",
              }}
            >
              <Avatar
                sx={{ cursor: "pointer" }}
                alt={user?.names}
                // src={user.picture}
                src={user?.picture}
                sizes="large"
              />
              <IconButton>
                <CameraAltIcon />
              </IconButton>
              <input
                accept=".jpg, .jpeg, .png"
                style={{
                  cursor: "pointer",
                  position: "absolute",
                  left: "0%",
                  top: "0%",
                  transform: "scale(5)",
                  opacity: "0",
                }}
                onChange={(e) => {
                  setFile(e.target.files[0]);
                }}
                type="file"
              />
            </Button>
            {file !== null ? (
              <Button
                variant="outlined"
                size="small"
                type="submit"
                sx={{ position: "absolute", left: "6.5rem", top: "2.5rem" }}
              >
                Tlcharger l'image
              </Button>
            ) : (
              ""
            )}
            {user.souscription === "" && (
              <Button variant="text" size="small">
                <a
                  style={{ textDecoration: "none" }}
                  href="https://me.fedapay.com/carte_12345"
                  target="blank"
                >
                  Compte non souscrire, veuilllez vous souscrire
                </a>
              </Button>
            )}
          </div>
        </form>

        {/* <div  >
       
      </div> */}
        <div>
          <Button onClick={handleClickOpen}>
            <IconButton>
              <LogoutIcon sx={{ color: "red" }} />
            </IconButton>
          </Button>
          <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
            <DialogTitle id="alert-dialog-title">{"Dconnexion"}</DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                Etes vous surs de vouloir vous deconnecter ?
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button sx={{ color: "red" }} onClick={handleClose}>
                Non
              </Button>
              <Button sx={{ color: "red" }} onClick={handleLogOut} autoFocus>
                Oui
              </Button>
            </DialogActions>
          </Dialog>
        </div>
      </Stack>
      <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={opens} autoHideDuration={10000} onClose={handleCloses}>
          <Alert onClose={handleCloses} severity="error" sx={{ width: "100%" }}>
            {error}
          </Alert>
        </Snackbar>
      </Stack>
    </>
  );
}
