import axios from 'axios';

import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { setGetUser } from '../../features/user.reducers';



import * as React from 'react';
import HomePartition from './partition/Partition-Home.jsx';





export default function Home() {
  ;
    const user = useSelector((state) => state.user.user)
    const dispatch = useDispatch()
    const history = useNavigate()
     

  const getUserWithVerifyToken = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
      withCredentials: true,
    });
  
    const data = await res.data;
    return data;
  };


    React.useEffect(() => {
    getUserWithVerifyToken().then((data) => {
      dispatch(setGetUser(data.user)); 
      console.log(data);
    }).catch((error) => {
      setTimeout(() => {
        console.log(error);
          history("/login");
        }, 5000);
     
    });
    }, [dispatch,history]);
    
    
 
   



    return (
      
      
      
        <>
          {user?.isAdmin && 
            <>
             
          <HomePartition />
            </>}
       
        
   
        </>
         
    
  
  );
}

