import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';

import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import axios from 'axios';
import { Button, Typography } from '@mui/material';
import { sendRquest } from '../../../app/verify';
import { setgetAll } from '../../../features/users.reducers';
import { useDispatch } from 'react-redux';
export default function BasicTable({ el }) {
 const dispatch = useDispatch()
    const handleSubmit = (event) => {
 
    
  event.preventDefault()
       axios
      .post(`${process.env.REACT_APP_URL_USER}/liste/${el?._id}`)
      .then((res) => {
         sendRquest().then((data) => {
        dispatch(setgetAll(data));
        
      });

        
        
      })
         .catch((error) => {
           alert(error.response.data)
           console.log(error.response.data);
           
      });
    

 
  };
  
  return (

    <TableContainer component={Paper}>
      
      
        <Table aria-label="simple table">
        <TableBody>
        
            <TableRow
              key={el?._id}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell width='100px' component="th" scope="row">
                {el?.firstName}
              </TableCell>
            <TableCell width='200px' align="left">{el?.lastName}</TableCell>
              <TableCell width='100px' align="left">{el?.identification}</TableCell>
            {el?.status === "" && <TableCell width='100px' onClick={handleSubmit} align="center"><Button>valider</Button></TableCell>}  
         
             {el?.status ==="A l'heure" && <TableCell width='100px' align="center"><Typography color='green'>{el?.status}</Typography></TableCell>  }
            {el?.status ==="En retard" && <TableCell width='100px' align="center"><Typography color='secondary'>{el?.status}</Typography></TableCell>  }
            {el?.status ==="Absent" && <TableCell width='100px' align="center"><Typography color='error'>{el?.status}</Typography></TableCell>  }
            <TableCell width='100px'>{el?.heure}</TableCell>
            </TableRow>
          
        </TableBody>
      </Table>
    </TableContainer> 
  );
}
