import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Link from "@mui/material/Link";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import axios from "axios";
import {
  Alert,
  FormControl,
  InputAdornment,
  Snackbar,
  Stack,
} from "@mui/material";
import { NavLink } from "react-router-dom";
import Navbar from "../NavbarAdmin";
import { useSelector } from "react-redux";

function Copyright(props) {
  return (
    <Typography
      variant="body2"
      color="text.secondary"
      align="center"
      {...props}
    >
      {"Copyright  "}
      <Link color="inherit" href="">
   FanFare GQP
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const theme = createTheme();
export default function Validate() {
  const [error, setError] = React.useState("");
const isAdmin  = useSelector(state=>state.user.user?.isAdmin)

  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false) 
  const [loading,setLoading] = React.useState(false)

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setLoading(true)
    setError("");
    if (!navigator.onLine) {
        setOpen(true);
      setError(
        "Si la connexion n'est pas active, vrifiez la connexion"
      );
    }
    
  
    
      const data = new FormData(event.currentTarget);
   
       axios
      .post(`${process.env.REACT_APP_URL_USER}/validate`, {
        idUser: data.get("idUser"),
        idTransaction: data.get("idTransaction"),
      })
      .then((res) => {
        console.log(res.data.message);
        setLoading(false)
        setResToHome(true)
        setOpen(true);
        setsuccees(res.data.message);

        // setTimeout(() => {
        //   history("/home");
        // }, 2000);
      })
         .catch((error) => {
        setLoading(false)
        setOpen(true);
        if(error.response.data.message) setError(error.response.data.message);
  
      });
    

 
  };

  return (
    <>
      {
     isAdmin &&
    <ThemeProvider theme={theme}>
      <Grid container component="main" sx={{ height: "100vh" }}>
        <CssBaseline />
        <Navbar/>
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: `url('./assets/login.jpg')`,
            objectFit:'cover',
            backgroundRepeat: "no-repeat",
            backgroundColor: (t) =>
              t.palette.mode === "light"
                ? t.palette.grey[50]
                : t.palette.grey[900],
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        />

        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            
            </Avatar>
            <Typography  component="h1" variant="h6" textTransform="uppercase">
              Valider la transaction
            </Typography>
            <Box
              component="form"
              noValidate
              onSubmit={handleSubmit}
              sx={{ mt: 1 }}
            >
              <TextField
                margin="normal"
                required
                fullWidth
                id="idUser"
                label="idUser"
                name="idUser"
                autoComplete="current"
                autoFocus
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonOutlineIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                name="idTransaction"
                label="idTransaction"
          
                id="idTransaction"
                autoComplete="current-idTransaction"
               
              />
              <FormControl
                sx={{ m: 1, width: "25ch" }}
                variant="filled"
              ></FormControl>
           
              {
                loading ?<Button
                type="submit"
                fullWidth
                  variant="contained"
                  disabled
                sx={{
                  mt: 3, mb: 2, background: {
                
                } }} 
              
              >
                loading......
              </Button> : <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{
                  mt: 3, mb: 2, background: {
                    md: "#DD9B01", xs: "#DD9B01", "&:hover": {
                      background : "#6D4826"
                    }
                } }}
              
              >
                connecter
              </Button>
             }
              
              <Grid container>
                <Grid item xs>
                  <NavLink to="/forget">
                    <Link variant="subtitle2">Mot de passe oubli?</Link>
                  </NavLink>
                </Grid>
                
                <Grid item xs>
                  <NavLink to="/register">
                    <Link variant="subtitle2">
                      Je n'ai pas un compte? Je m'inscrire 
                    </Link>
                  </NavLink>
                </Grid>
              </Grid>

              <Copyright sx={{ mt: 5 }} />
            </Box>
          </Box>
        </Grid>
      </Grid>
  { resToHome  ? <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="success"
            sx={{ width: "100%" }}
          >
            {succees} 
          
          </Alert>
        </Snackbar>

      </Stack>
      :
      <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="error"
            sx={{ width: "100%" }}
          >
            
            {error}
          </Alert>
        </Snackbar>
      </Stack>}
    </ThemeProvider>
 }
    </>
  );
}
