import { Button, FormControl, Input, InputAdornment, InputLabel, Table, TableCell, TableHead, TableRow, Typography } from '@mui/material';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setGetUser } from '../../../features/user.reducers';
import { setgetAll } from '../../../features/users.reducers';
import Navbar from '../NavbarAdmin';
import BasicTable from './Table';
import { getUserWithVerifyToken,sendRquest } from '../../../app/verify';
import { dateParser } from '../../../utils/dateParser';


const Parent = () => {
     const users = useSelector((state) => state.users.users);
  const user = useSelector((state) => state.user.user);
  

  const dispatch = useDispatch();
   const [searchValue, setSearchValue] = useState('');
   const [send, setSend] = useState(false);
   const [sendMessage, setSendMessage] = useState(false);
 
  const SendPdfListe = async () => {
    await axios.get(`${process.env.REACT_APP_URL_USER}/liste/${user._id}`).then((res)=> {
      console.log(res)
      setSend(true)
      setSendMessage(res.data.message)
      
    }).catch(err => {
      console.log(err)
      setSend(true)
      setSendMessage(err.response.data.message)
    

    })
  }
  // eslint-disable-next-line array-callback-return
  const nombrePresent = users?.filter((el) => {
            if (el?.status === "prsent")
              return true
          })
  // eslint-disable-next-line array-callback-return
  const nombreRetard = users?.filter((el) => {
            if (el?.status === "en retard")
              return true
          })
  // eslint-disable-next-line array-callback-return
  const nombreAbsent = users?.filter((el) => {
            if (el?.status === "Absent")
              return true
          })
  useEffect(() => {
    getUserWithVerifyToken().then((data) => {
      dispatch(setGetUser(data.user))
    });
  },[dispatch])
useEffect(() => {
      sendRquest().then((data) => {
        dispatch(setgetAll(data));
        
      });
}, [dispatch])
    return (
      <>
       {user?.isAdmin && <div>
        <Navbar/>
        <Typography>Rechercher votre code </Typography>
         <FormControl fullWidth sx={{ m: 1 }} variant="standard">
          <InputLabel  htmlFor="standard-adornment-amount">Votre Code Personnel</InputLabel>
          <Input
            value={searchValue} onChange={(e) => setSearchValue(e.target.value)}
            id="standard-adornment-amount"
            startAdornment={<InputAdornment position="start">code</InputAdornment>}
          />
        </FormControl>
  
              <Table aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Nom</TableCell>
            <TableCell align="left">Prnom</TableCell>
            <TableCell align="left">Code</TableCell>
            <TableCell align="center">Statut</TableCell>
            <TableCell align="right">heure</TableCell>
          </TableRow>
        </TableHead>
                  </Table>

                    
        {users
          ?.filter(item => item.identification?.toLowerCase().includes(searchValue.toLowerCase()) && item.isSuperAdmin !== true
   )
          .map((el) => {
              return (
               <BasicTable key={el?._id} el={el} /> 
                )
            })
        }
          <br></br>
          <>
            <Typography>{nombrePresent.length} prsent au rptition du { dateParser(new Date(Date.now()))} sur {users?.length} </Typography>
          
            <Typography>{nombreRetard.length} sont vnu en rtard au rptition { dateParser( Date.now())}</Typography>
            <Typography>{nombreAbsent.length} absent au rptition { dateParser( Date.now())}</Typography>
          
            </>
          {send ? <Typography>
         {sendMessage}
          </Typography> : <Button sx={{ transform: 'translateX(-50%)', marginLeft: '50%' }}
          variant='contained' onClick={SendPdfListe}>Envoyer le pdf</Button>} 
     
       
        </div>}
      </>
    );
};

export default Parent;