import { createSlice } from "@reduxjs/toolkit";

export const userSlice = createSlice({
  name: "user",
  initialState: {
    user: false,
  },
  reducers: {
    /**Rcuperer les donnes de l'user */
    setGetUser: (state, { payload }) => {
      state.user = payload;
    },
    /**Dconnexion de l'utilisateur */
    logOut: (state, { payload }) => {
      state.user = true;
    },
    /**Modification du profil de l'user */
    updateProfil: (state, { payload }) => {
      state.user.push(payload);
    },
    /**Tlcharger un fichier par l'utilisateur */
    uploadPicture: (state, { payload }) => {
      return {
        ...state,
        picture: payload,
      };
    },
  },
});

export const { setGetUser, logOut, uploadPicture, updateProfil } =
  userSlice.actions;
export default userSlice.reducer;
