import { createSlice } from "@reduxjs/toolkit";

export const partitionSlice = createSlice({
  name: "partitions",
  initialState: {
    partitions: [],
  },
  reducers: {
    /**Rcuperer touts les partitions */
    getAllpartition: (state, { payload }) => {
      state.partitions = payload;
    },
    /**Ajouter une partition */
    addpartition: (state, { payload }) => {
      state.partitions?.push(payload);
    },
  },
});

export const { getAllpartition, addpartition } = partitionSlice.actions;
export default partitionSlice.reducer;
