/**Function de vrification du token */

import axios from "axios";
/**Vrification du token */
export const getUserWithVerifyToken = async () => {
  const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
    withCredentials: true,
  });

  const data = await res.data;
  return data;
};

/**Rcupration de touts les chants */
export const sendRquestPost = async () => {
  const res = await axios.get(`${process.env.REACT_APP_URL_PARTITION}/read`, {
    withCredentials: true,
  });

  const data = await res.data;
  return data;
};

/**Rcupration de touts les utilisateur */
export const sendRquest = async () => {
  const res = await axios.get(`${process.env.REACT_APP_URL_USER}`, {
    withCredentials: true,
  });
  const data = await res.data;
  return data;
};
