import React from "react";
import { useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import MobileStepper from "@mui/material/MobileStepper";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import KeyboardArrowLeft from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRight from "@mui/icons-material/KeyboardArrowRight";
import SwipeableViews from "react-swipeable-views";
import { autoPlay } from "react-swipeable-views-utils";
import { useSelector } from "react-redux";
const AutoPlaySwipeableViews = autoPlay(SwipeableViews);
/**Les images de notre header */
const images = [
  {
    label: "Lisez vos partitions partout",
    imgPath: "./assets/portee.jpg",
  },
  {
    label: "Avoir sa carte membre unique",
    imgPath: "./assets/membre.jpg",
  },
  {
    label: 'Profitez des services du centre" ',
    imgPath: "./assets/service.jpg",
  },
  {
    label: "Gerer vos transactions en toute scurit",
    imgPath: "./assets/transaction.jpg",
  },
];

const Header = () => {
  /**State global */
  const user = useSelector((state) => state.user?.user);
  /**State local */
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);
  const maxSteps = images.length;
  /**Function de pagination */
  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  return (
    <div>
      <Typography sx={{ marginLeft: "15px" }} variant="h6">
        Bienvennue {user?.names}
      </Typography>
      <Box sx={{ maxWidth: 2000, flexGrow: 1 }}>
        <Paper
          square
          sx={{
            display: "flex",
            alignItems: "center",
            height: 25,
            maxWidth: 2000,
            pl: 2,
            bgcolor: "#F8B216",
          }}
        >
          <Typography
            variant="subtitle1"
            sx={{ color: "black", textAlign: "center" }}
          >
            {images[activeStep].label}
          </Typography>
        </Paper>
        <AutoPlaySwipeableViews
          axis={theme.direction === "rtl" ? "x-reverse" : "x"}
          index={activeStep}
          onChangeIndex={handleStepChange}
          enableMouseEvents
        >
          {images.map((step, index) => (
            <div
              style={{ width: "100%", overflowClipBox: "hidden" }}
              key={step.label}
            >
              {Math.abs(activeStep - index) <= 2 ? (
                <Box
                  component="img"
                  sx={{
                    height: 255,
                    display: "block",
                    // maxWidth: 2000,
                    width: "100%",

                    objectFit: "cover",
                  }}
                  src={step.imgPath}
                  alt={step.label}
                />
              ) : null}
            </div>
          ))}
        </AutoPlaySwipeableViews>
        <MobileStepper
          steps={maxSteps}
          position="static"
          activeStep={activeStep}
          nextButton={
            <Button
              size="small"
              onClick={handleNext}
              disabled={activeStep === maxSteps - 1}
              // variant="outlined"
            >
              Next
              {theme.direction === "rtl" ? (
                <KeyboardArrowLeft />
              ) : (
                <KeyboardArrowRight />
              )}
            </Button>
          }
          backButton={
            <Button
              size="small"
              onClick={handleBack}
              disabled={activeStep === 0}
              // variant="outlined"
            >
              {theme.direction === "rtl" ? (
                <KeyboardArrowRight />
              ) : (
                <KeyboardArrowLeft />
              )}
              Back
            </Button>
          }
        />
      </Box>
    </div>
  );
};

export default Header;
