import { Icon, Paper, Typography } from "@mui/material";
import { Stack } from "@mui/system";
import MusicNoteIcon from "@mui/icons-material/MusicNote";
import PersonAddIcon from "@mui/icons-material/PersonAdd";
import SchoolIcon from "@mui/icons-material/School";

import CurrencyExchangeIcon from "@mui/icons-material/CurrencyExchange";
import React from "react";
import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";

const Card = () => {
  const user = useSelector((state) => state.user?.user);

  return (
    <div>
      <br />
      <br />
      <Stack
        direction="row"
        justifyContent="space-evenly"
        flexWrap="nowrap"
        alignItems="center"
        spacing={2}
      >
        <NavLink to="/partition">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#C1233A",
            }}
          >
            <Typography
              variant="body1"
              sx={{
                position: "absolute",
                lzft: "0",
                bottom: "0",
                color: "white",
                margin: "10px",
              }}
              gutterBottom
            >
              Vos Partitions
            </Typography>
            <Icon>
              <MusicNoteIcon
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "white",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>
        <NavLink to="/carte">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#EF7720",
            }}
          >
            <Typography
              variant="body1"
              sx={{
                position: "absolute",
                lzft: "0",
                bottom: "0",
                color: "white",
                margin: "10px",
              }}
              gutterBottom
            >
              Carte Personnel
            </Typography>
            <Icon>
              <PersonAddIcon
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "white",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>
      </Stack>
      <br />
      <br />
      <Stack
        direction="row"
        justifyContent="space-evenly"
        flexWrap="nowrap"
        alignItems="center"
        spacing={2}
      >
        <NavLink to="/education">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#109836",
            }}
          >
            <Typography
              variant="body1"
              sx={{
                position: "absolute",
                lzft: "0",
                bottom: "0",
                color: "white",
                margin: "10px",
              }}
              gutterBottom
            >
              Mes Notes et Souscription
            </Typography>
            <Icon>
              <SchoolIcon
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "white",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>
        <NavLink to="/transaction">
          <Paper
            elevation={3}
            sx={{
              width: "160px",
              height: "150px",
              position: "relative",
              cursor: "pointer",
              backgroundColor: "#2FABB5",
            }}
          >
            <Typography
              variant="body1"
              sx={{
                position: "absolute",
                lzft: "0",
                bottom: "0",
                color: "white",
                margin: "10px",
              }}
              gutterBottom
            >
              Mes Transactions
            </Typography>
            <Icon>
              <CurrencyExchangeIcon
                sx={{
                  position: "absolute",
                  right: "0",
                  top: "0",
                  fontSize: "3rem",
                  color: "white",
                  margin: "5px",
                }}
              />
            </Icon>
          </Paper>
        </NavLink>
      </Stack>
    </div>
  );
};

export default Card;
