/* eslint-disable no-mixed-operators */
import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Link from "@mui/material/Link";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";

import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import PasswordIcon from "@mui/icons-material/Password";

import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import axios from "axios";
import { Alert, InputAdornment, Snackbar, Stack } from "@mui/material";
import { NavLink, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { getUserWithVerifyToken } from "../../../app/verify";

const theme = createTheme();
export default function Validation() {
  /**State globale */
  const user = useSelector((state) => state.user?.user);
  /**state locale */
  const history = useNavigate();
  const [error, setError] = React.useState("");
  const [open, setOpen] = React.useState(false);
  const [succees, setsuccees] = React.useState("");
  const [resToHome, setResToHome] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  /**Mui function */
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setLoading(true);
    setError("");
    if (!navigator.onLine) {
      setOpen(true);
      setError("Si la connexion n'est pas active, vrifiez la connexion");
    }

    /**Function de validation de l'id de transaction */
    const data = new FormData(event.currentTarget);
    console.log({
      idVerified: data.get("idVerified"),
    });
    axios
      .post(`${process.env.REACT_APP_URL_USER}/verified/${user?._id}`, {
        idVerified: data.get("idVerified"),
      })
      .then((res) => {
        console.log(res.data.message);
        setLoading(false);
        setResToHome(true);
        setOpen(true);
        setsuccees(res.data.message);
        setTimeout(() => {
          history("/carte");
        }, 5000);
          axios.get(`${process.env.REACT_APP_URL_USER}/jwt`);
      })
      .catch((error) => {
        console.log(error);
        setLoading(false);
        setOpen(true);
        setError(error.response.data.message);
      })
      .then(() => {
        axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
          withCredentials: true,
        });
      });
  };
  React.useEffect(() => {
    getUserWithVerifyToken()
  })

  return (
    <>
      {/* les conditions pour empcher l'utilisateur de ne pas saisir l'id de
      transaction deux en passant par l'url */}
      {user?.idVerified === "1" || (user?.idVerified === "0" && history("/home"))}
      {user?.idVerified === "" && (
        <ThemeProvider theme={theme}>
          <Grid container component="main" sx={{ height: "100vh" }}>
            <CssBaseline />
            <Grid
              item
              xs={false}
              sm={4}
              md={7}
              sx={{
                backgroundImage: `url('./assets/register.jpg')`,
                objectFit: "cover",
                backgroundRepeat: "no-repeat",
                backgroundColor: (t) =>
                  t.palette.mode === "light"
                    ? t.palette.grey[50]
                    : t.palette.grey[900],
                backgroundSize: "cover",
                backgroundPosition: "center",
              }}
            />

            <Grid
              item
              xs={12}
              sm={8}
              md={5}
              component={Paper}
              elevation={6}
              square
            >
              <Box
                sx={{
                  my: 8,
                  mx: 4,
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
                  <PasswordIcon />
                </Avatar>
                <Typography
                  component="h1"
                  variant="h6"
                  textTransform="uppercase"
                >
                  Mettez l'id de transaction
                </Typography>
                <Box
                  component="form"
                  autoComplete=""
                  onSubmit={handleSubmit}
                  sx={{ mt: 1 }}
                >
                  <TextField
                    margin="normal"
                    required
                    fullWidth
                    id="idVerified"
                    label="Id transaction"
                    helperText="Saisissez l'id de transaction  10 chiffre"
                    name="idVerified"
                    autoComplete="idVerified"
                    autoFocus
                    type="number"
                    inputProps={{ maxLength: 10, minLength: 10 }}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <PersonOutlineIcon />
                        </InputAdornment>
                      ),
                    }}
                  />
                  <NavLink to="/carte">
                    <Link variant="subtitle2">Retour  l'accueil</Link>
                  </NavLink>

                  {loading ? (
                    <Button
                      type="submit"
                      fullWidth
                      disabled
                      sx={{
                        mt: 3,
                        mb: 2,
                        background: {},
                      }}
                    >
                      loading......
                    </Button>
                  ) : (
                    <Button
                      type="submit"
                      fullWidth
                      sx={{
                        mt: 3,
                        mb: 2,
                        background: {
                          md: "#DD9B01",
                          xs: "#DD9B01",
                          "&:hover": {
                            background: "#6D4826",
                          },
                        },
                      }}
                      variant="contained"
                    >
                      envoyer
                    </Button>
                  )}
                </Box>
              </Box>
            </Grid>
          </Grid>
          {resToHome ? (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={5000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="success"
                  sx={{ width: "100%" }}
                >
                  {succees}
                </Alert>
              </Snackbar>
            </Stack>
          ) : (
            <Stack spacing={2} sx={{ width: "100%" }}>
              <Snackbar
                open={open}
                autoHideDuration={5000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="error"
                  sx={{ width: "100%" }}
                >
                  {error}
                </Alert>
              </Snackbar>
            </Stack>
          )}
        </ThemeProvider>
      )}
    </>
  );
}
