import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { setGetUser } from "../../../features/user.reducers";
import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { getUserWithVerifyToken } from "../../../app/verify";
import Navbar from "../NavbarAdmin";

const theme = createTheme();

export default function HomePartition() {
  const user = useSelector((state) => state.user.user);
  /**State local */
  const [titles, setTitles] = React.useState("");
  const [auteur, setauteur] = React.useState("");
  const [gamme, setgamme] = React.useState("");
  const [file, setFile] = React.useState();
  const [sucess, setSucess] = React.useState("");
  const [error, setError] = React.useState("");
  const dispatch = useDispatch();
  const history = useNavigate();

  React.useEffect(() => {
    getUserWithVerifyToken()
      .then((data) => {
        dispatch(setGetUser(data.user));
      })
      .catch((error) => {
        setTimeout(() => {
          history("/login");
        }, 2000);
      });
  }, [dispatch, history]);

  /**Function pour crer nos partitions */
  const handlePostUpload = async () => {
    const data = new FormData();
    data.append("posterId", user?.email);
    data.append("title", titles);
    data.append("partition", file);
    data.append("gamme", gamme);
    data.append("auteur", auteur);
    const res = await axios.post( `${process.env.REACT_APP_URL_USER}/create`, data
    );
    const datas = await res.data;
    return datas;
  };
  const handlePost = async () => {
    handlePostUpload()
      .then((res) => {
        setSucess(res.data.message);
       
      })
      .catch((err) => {
      console.log(err);
      });
  };

  return (
    <>
  
        <ThemeProvider theme={theme}>
          <Navbar />
          <Container component="main" maxWidth="xs">
            <CssBaseline />
            <Box
              sx={{
                marginTop: 8,
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
                <LockOutlinedIcon />
              </Avatar>
              <Typography component="h1" variant="h5">
                Les chants
              </Typography>
              <Box
                component="form"
                onSubmit={handlePost}
                noValidate
                sx={{ mt: 3 }}
              >
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      id="title"
                      label="Title"
                      name="title"
                      onChange={(e) => setTitles(e.target.value)}
                      value={titles}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      id="auteur"
                      label="auteur"
                      name="auteur"
                      value={auteur}
                      onChange={(e) => {
                        setauteur(e.target.value);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      id="gamme"
                      label="gamme"
                      name="gamme"
                      value={gamme}
                      onChange={(e) => {
                        setgamme(e.target.value);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      name="file"
                      onChange={(e) => {
                        setFile(e.target.files[0]);
                      }}
                      type="file"
                      id="file"
                      autoComplete="new-file"
                      accept=".jpg, .jpeg, .png"
                    />
                  </Grid>
                </Grid>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{ mt: 3, mb: 2 }}
                >
                  Envoyer
                </Button>
              </Box>
            </Box>
            <Typography>{sucess}</Typography>
            <Typography>{error}</Typography>
          </Container>
        </ThemeProvider>
      
    </>
  );
}
