import { configureStore } from "@reduxjs/toolkit";
import userReducer from "../features/user.reducers";
import partitionReducer from "../features/partition.reducers";
import usersReducers from "../features/users.reducers";

export default configureStore({
  reducer: {
    user: userReducer, // Reducer de de l'utilisateur
    partitions: partitionReducer, //Reducer des partitions
    users: usersReducers, // Reducer de touts les utilisateurs
  },
});
