import { Button, Card, Icon, Paper, Typography } from "@mui/material";
import React from "react";
import Box from "@mui/material/Box";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepLabel from "@mui/material/StepLabel";
import StepContent from "@mui/material/StepContent";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";
import { Stack } from "@mui/system";
/**Tablaeu des tapes csuivre pour avoir  sa carte */
const stepsCartedeux = [
  {
    label: "Vrification",
    description: `Pour avoir votre carte numrique d'identification personnelle, vous devez vrifier si vos identifiants inscrits sur la carte sont complets (nom, prnom, email, tl, etc.). Si ce n'est le cas, allez dans profil et mettez vos identifiants  jour`,
    img: `./procedure/carte.png`,
  },
  {
    label: "Payement",
    description: `Si vos informations sont correctes, vous pouvez procder au paiement via FedaPay en cliquant sur le bouton "diter ma carte". Aprs avoir effectu le paiement, vous devrez remplir un formulaire en mettant l'ID de transaction reu de MTN associ  votre paiement.`,
    img: `./procedure/payement.png`,
  },
  {
    label: "Statut",
    description: `Une fois que vous avez soumis le formulaire, actualiser la page, votre statut sera mis  jour pour indiquer "Attente". Cela signifie que votre demande est en cours d'examen et que vous devrez attendre les 24 heures suivantes pour savoir si votre demande a t approuve. S'il arrivait que vous mettez mal l'id de transaction, crivez aux administrateurs en envoyant le nouveau id. Aprs deux essais, vous ne pouvez plus valider votre carte, l'opration serait considrer comme transaction non effectue.`,
    img: `./procedure/attente.png`,
  },
  {
    label: "Validation ",
    description: `Si votre demande est approuve aprs les 24 heures, vous recevrez un mail vous indiquant que votre transaction est effectue avec succs, votre statut sera mis  jour pour indiquer "Russi". Il est important de suivre toutes les tapes correctement pour garantir le succs de votre demande.`,
    img: `./procedure/reussi.png`,
  },
];

const CartePeronnel = () => {
  /**State global */
  const user = useSelector((state) => state.user?.user);
  /**State local */
  /**Mui function */
  const [activeStepsCartedeux, setActiveStepsCartedeux] = React.useState(0);
  const handleNextCarte = () => {
    setActiveStepsCartedeux((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBackCarte = () => {
    setActiveStepsCartedeux((prevActiveStep) => prevActiveStep - 1);
  };

  const handleResetCarte = () => {
    setActiveStepsCartedeux(0);
  };
  return (
    <div>
      <NavLink to="/home">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>
      <Typography variant="h5" sx={{ textAlign: "center" }}>
        Carte Personnel
      </Typography>

      <br></br>
      <Box fullwidth>
        <Typography
          variant="body1"
          textAlign="center"
          sx={{ textDecoration: "underline" }}
        >
          Comment diter sa carte
        </Typography>
        <Stepper activeStep={activeStepsCartedeux} orientation="vertical">
          {stepsCartedeux.map((step, index) => (
            <Step key={step.label}>
              <StepLabel
                optional={
                  index === 4 ? (
                    <Typography variant="caption">Last step</Typography>
                  ) : null
                }
              >
                <Typography variant="subtitle1"> {step.label} </Typography>
              </StepLabel>
              <StepContent>
                <Typography variant="body2">{step.description}</Typography>
                <Box sx={{ mb: 2 }}>
                  <img
                    src={step.img}
                    alt="img"
                    style={{ width: "100%", height: "150px" }}
                  />
                  <div>
                    <Button
                      variant="outlined"
                      onClick={handleNextCarte}
                      sx={{ mt: 1, mr: 1 }}
                    >
                      {index === stepsCartedeux.length - 1 ? "Fin" : "Continue"}
                    </Button>
                    <Button
                      disabled={index === 0}
                      onClick={handleBackCarte}
                      sx={{ mt: 1, mr: 1 }}
                    >
                      retour
                    </Button>
                  </div>
                </Box>
              </StepContent>
            </Step>
          ))}
        </Stepper>
        {activeStepsCartedeux === stepsCartedeux.length && (
          <Paper square elevation={0} sx={{ p: 3 }}>
            <Typography>
              Procdez maintenant au payement en cliquant sur diter ma carte
            </Typography>
            <Button onClick={handleResetCarte} sx={{ mt: 1, mr: 1 }}>
              Relire
            </Button>
          </Paper>
        )}
      </Box>
      <Card
        sx={{
          width: "90mm",
          height: "55mm",
          alignItems: "center",
          margin: "auto",
          position: "relative",
          background: "linear-gradient(to right, green, yellow,red)",
        }}
      >
        <Typography
          align="center"
          variant="subtitle1"
          textTransform="uppercase"
        >
          {" "}
          Republique du Bnin
        </Typography>
        <Typography align="center" variant="body2" textTransform="uppercase">
          {" "}
          Carte numrique d'identification Peronnel
        </Typography>
        <Typography
          align="center"
          variant="body1"
          color="secondary"
          textTransform="uppercase"
        >
          {" "}
          N LGP{user?.identification}
        </Typography>
        <img
          style={{ position: "absolute", top: "2%", left: "2%", width: "20px" }}
          src="logo.jpg"
          alt="logo grce qui parle"
        />
        <img
          style={{
            position: "absolute",
            top: "28%",
            right: "2%",
            width: "30px",
          }}
          src={user?.picture}
          alt="image_personnel"
        />
        <img
          style={{
            position: "absolute",
            bottom: "5%",
            right: "2%",
            width: "75px",
          }}
          src={user?.qrCode}
          alt="image_personnel"
        />
        <Stack direction="row" spacing={5}>
          <div>
            <Typography fontWeight="bolder" variant="subtitle2">
              Nom
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              Prenom
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              Instrument
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              Partition
            </Typography>
            {user?.sexe && (
              <Typography fontWeight="bolder" variant="subtitle2">
                Sexe
              </Typography>
            )}

            <Typography fontWeight="bolder" variant="subtitle2">
              Profession
            </Typography>
            <Typography fontWeight="bolder" variant="subtitle2">
              Tel
            </Typography>
          </div>
          <div>
            <Typography variant="subtitle2">{user?.firstName}</Typography>
            <Typography variant="subtitle2">{user?.lastName}</Typography>
            <Typography variant="subtitle2">{user?.instrument}</Typography>
            <Typography variant="subtitle2">{user?.partition}</Typography>
            {user?.sexe && (
              <Typography variant="subtitle2">{user?.sexe}</Typography>
            )}

            <Typography variant="subtitle2">{user?.profession}</Typography>
            <Typography variant="subtitle2">+229 {user?.tel}</Typography>
          </div>
        </Stack>
      </Card>
      <br></br>
      <Card
        sx={{
          width: "90mm",
          height: "55mm",
          alignItems: "center",
          margin: "auto",
          position: "relative",
          background: "linear-gradient(to right, green, yellow,red)",
        }}
      >
        <Stack
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            alignContent: "center",
          }}
        >
          <br />
          <br />
          <Typography align="center" variant="h6" textTransform="uppercase">
            {" "}
            FANFARE LA GRACE PARLE
          </Typography>
          <Typography align="center" variant="subtitle1">
            Cette carte est strictement personnelle. En cas de perte vous devrez
            contactez les administrateurs du groupe:
          </Typography>
          <Typography align="center" variant="subtitle1">
            Tel : +229 00000000
          </Typography>
          <Typography align="center" variant="subtitle1">
            Email: grace@1234parle@gmail.com
          </Typography>
          <Typography align="center" variant="subtitle1">
            Site Web: gracequiparle.com
          </Typography>
        </Stack>
      </Card>
      <br></br>
      <div>
        {user?.idVerified !== "1" &&
          user?.idVerified !== "0" &&
          user?.idVerified !== "" && (
            <Button
              color="success"
              sx={{ marginLeft: "50%", transform: "translateX(-50%)" }}
              variant="contained"
            >
              En attente....
            </Button>
          )}
      </div>

      <>
        {user?.idVerified === "0" && (
          <Button
            sx={{ marginLeft: "50%", transform: "translateX(-50%)" }}
            color="error"
            variant="contained"
          >
            Echou
          </Button>
        )}

        {user.idVerified === "1" && (
          <Button
            sx={{ marginLeft: "50%", transform: "translateX(-50%)" }}
            color="success"
            variant="contained"
          >
            Reussie
          </Button>
        )}
        {user?.idVerified === "" && (
          <Stack
            spacing={1}
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
            direction="row"
          >
            <Button
              variant="contained"
              size="small"
             
            
            >
              <a style={{ textDecoration: 'none' }} href="https://me.fedapay.com/carte_12345"
                target='blank'>Editer ma carte</a>
            </Button>

            <NavLink style={{textDecoration:'none'}} to="/validation">
              <Button  size="small" variant="contained">
                Valider la transaction 
              </Button>
            </NavLink>
          </Stack>
        )}
      </>
    </div>
  );
};

export default CartePeronnel;
