import React from "react";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { Icon, Typography } from "@mui/material";
import { NavLink } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getAllpartition } from "../../../features/partition.reducers";
import { useEffect } from "react";
import CardPartition from "../card/CardPartition";
import { Stack } from "@mui/system";

import axios from "axios";
const Partition = () => {
  /**State global */
  const dispatch = useDispatch();
  const partitions = useSelector((state) => state.partitions.partitions);
  /**Rcuperer touts nos partitions */
  const sendRquestPost = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/read`, {
      withCredentials: true,
    });
    const data = await res.data;
    return data;
  };
  /**Au chargement de la page */
  useEffect(() => {
    sendRquestPost().then((data) => {
      dispatch(getAllpartition(data));
    });
  }, [dispatch]);

  return (
    <div>
      <NavLink to="/home">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>
      <Typography variant="h5" sx={{ textAlign: "center" }}>
        Touts vos chants
      </Typography>
      <br></br>
      <Stack
        direction="row"
        spacing={6}
        alignItems="center"
        justifyContent="center"
      >
        <Typography variant="h6">Titre</Typography>
        <Typography variant="h6">Auteur</Typography>
        <Typography variant="h6">Gamme</Typography>
      </Stack>
      <br></br>
      {partitions?.map((partition) => {
        return <CardPartition partition={partition} />;
      })}
    </div>
  );
};

export default Partition;
