import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";

// import MenuIcon from "@mui/icons-material/Menu";
import Container from "@mui/material/Container";



import { NavLink } from "react-router-dom";
import YoutubeSearchedForIcon from "@mui/icons-material/YoutubeSearchedFor";
import { useSelector } from "react-redux";

const pages = [
  {
    nom:"Liste-presence",
    link: "/admin/home-liste",
  },
  {
    nom: "Accueil",
    link: "/home",
  },
   {
    nom: "Utilisateurs",
    link: "/admin/home-users",
  },
 
];
const pagesDev = [
 
  {
    nom: "Partition",
    link: "/admin",
  },
  {
    nom: "Utilisateurs",
    link: "/admin/home-users",
  },
  {
    nom: "Validation",
    link: "/admin/home-validate",
  },

  {
    nom:"Liste-presence",
    link: "/admin/home-liste",
  },
  {
    nom: "Accueil",
    link: "/home",
  },
 
];
const pagesAdminPupitre = [
  {
    nom: "Evaluation",
    link: "/admin/home-note",
  },
   {
    nom: "Accueil",
    link: "/home",
  },

];


function Navbar() {

  const user = useSelector((state) => state.user?.user);
  
  return (
    <>
   
      
        {
        user.isAdmin && <AppBar position="sticky" sx={{ backgroundColor: "#DB9A02" }}>
            <Container maxWidth="xl">
              <Toolbar disableGutters>
                {/* <AdbIcon sx={{ display: { xs: "none", md: "flex" }, mr: 1 }} /> */}
                <Typography
                  variant="h6"
                  noWrap
                  component="a"
                  href="/home"
                  sx={{
                    mr: 2,
                    display: { xs: "flex" },
                    fontFamily: "monospace",
                    fontWeight: 700,
                    letterSpacing: ".3rem",
                    color: "inherit",
                    textDecoration: "none",
                  }}
                >
                  <YoutubeSearchedForIcon />
                </Typography>

                <Box sx={{ flexGrow: 1, display: { xs: "flex" } }}>
                  {pages.map((page) => (
                    <Typography
                      component={NavLink}
                      to={page.link}
                      key={page.link}
                      sx={{ marginRight: 1, color: "white", fontSize: "12px" }}
                      variant="outlined"
                    >
                      {<Typography>{page.nom}</Typography>}
                    </Typography>
                  ))}
                </Box>
              </Toolbar>
            </Container>
          </AppBar>
        }
        {
        user.isAdminDev && <AppBar position="sticky" sx={{ backgroundColor: "#DB9A02" }}>
            <Container maxWidth="xl">
              <Toolbar disableGutters>
                {/* <AdbIcon sx={{ display: { xs: "none", md: "flex" }, mr: 1 }} /> */}
                <Typography
                  variant="h6"
                  noWrap
                  component="a"
                  href="/home"
                  sx={{
                    mr: 2,
                    display: { xs: "flex" },
                    fontFamily: "monospace",
                    fontWeight: 700,
                    letterSpacing: ".3rem",
                    color: "inherit",
                    textDecoration: "none",
                  }}
                >
                  <YoutubeSearchedForIcon />
                </Typography>

                <Box sx={{ flexGrow: 1, display: { xs: "flex" } }}>
                  {pagesDev.map((page) => (
                    <Typography
                      component={NavLink}
                      to={page.link}
                      key={page.link}
                      sx={{ marginRight: 1, color: "white", fontSize: "12px" }}
                      variant="outlined"
                    >
                      {<Typography>{page.nom}</Typography>}
                    </Typography>
                  ))}
                </Box>
              </Toolbar>
            </Container>
          </AppBar>
        }

        {user.isAdminPupitre && <AppBar position="sticky" sx={{ backgroundColor: "#DB9A02" }}>
            <Container maxWidth="xl">
              <Toolbar disableGutters>
                {/* <AdbIcon sx={{ display: { xs: "none", md: "flex" }, mr: 1 }} /> */}
                <Typography
                  variant="h6"
                  noWrap
                  component="a"
                  href="/home"
                  sx={{
                    mr: 2,
                    display: { xs: "flex" },
                    fontFamily: "monospace",
                    fontWeight: 700,
                    letterSpacing: ".3rem",
                    color: "inherit",
                    textDecoration: "none",
                  }}
                >
                  <YoutubeSearchedForIcon />
                </Typography>

                <Box sx={{ flexGrow: 1, display: { xs: "flex" } }}>
                  {pagesAdminPupitre.map((page) => (
                    <Typography
                      component={NavLink}
                      to={page.link}
                      key={page.link}
                      sx={{ marginRight: 1, color: "white", fontSize: "12px" }}
                      variant="outlined"
                    >
                      {<Typography>{page.nom}</Typography>}
                    </Typography>
                  ))}
                </Box>
              </Toolbar>
            </Container>
          </AppBar>}
        
        </>
      
   
    
  );
}
export default Navbar;
